.register,
.login {
	width: fit-content;
	height: fit-content;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	gap: 1rem;
	margin: 0 auto;
	padding: 4rem;
	border-radius: 0.6rem;
	border: 1px solid lighten(#303030, 50);
}

.title {
	font-size: 3rem;
}

.authForm {
	margin-top: 1rem;
	width: fit-content;
	display: flex;
	flex-direction: column;
	gap: 2rem;

	label {
		width: 100%;
		font-size: 2rem;
		display: flex;
		flex-direction: column;
		font-size: 12px;

		input {
			max-width: 360px;
			min-width: 300px;
			margin: 0 auto;
		}
	}

	button {
		width: 100%;
		margin: 0 auto;
		padding: 0.9rem;
	}
}

.text_link {
	color: lighten(#303030, 40);
	text-decoration: underline;
	cursor: pointer;
}
